@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');
*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}
$light_green:#F6CB1D;
$dark_green:#F6CB1D;
$black:#181817;
$white:#fff;

.dflex{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
body{
  display: block;
  width:100%;
  height: 100%;
  background-color: $white;
  overflow-x: hidden; 
  font-family: 'Poppins', sans-serif;
}
header{
  @extend .dflex;
  justify-content: space-between;
  padding: 0 13%;
  height: 120px;
  .logo-wrapper{
    font-size: 32px;
    font-weight: 600;
    color:$dark_green;
    span{
      color:$black;
      span{
        color:$dark_green;
      }
    }
  }
  .menu-container{
    @extend .dflex;
    justify-content: flex-end;
    width: 600px;
    height: 56px;
    span{
      display: inline-flex;
      align-items: center;
      justify-content: flex-start;
      height: 100%;
      position: relative;
      +span{
        &:before{
          content:"";
          position: absolute;
          width: 1.90px;
          height: 50%;
          background-color: #bdbdbd;
          left: -36px;
          top:50%;
          transform: translateY(-50%);
        }
      }
      .icon{
        display: inline-flex;
        +.icon{
          margin-left: 20px;

        }
      }
      &:nth-child(1){
        width: 22%;
      }
      &:nth-child(2){
        width: 42%;
        .icon{
          margin-right:20px;
        }
      }
      &.menu{
        @extend .dflex;
        width:56px;
        height: 56px;
        background-color: $dark_green;
        border-radius: 10px;
        color: $white;
        cursor: pointer;
        flex-direction: column;
        span{
          display: block;
          width: 20px;
          height: 3px;
          background-color: $black;
          border-radius: 3px;
          position: relative;
          +span{
            margin-top: 5px;
            &:before{
              content:none;
            }
          }
          &:nth-child(2){
            margin-left: 8px;
          }
        }
      }
    }
  }
}



.content-wrapper{
  @extend .dflex;
  width: 80%;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  background-color: $light_green;
  clip-path:polygon(0 0, 100% 0, 100% 100%, 0 100%);
  border-radius: 15px;
  padding:3%;
  .left-content-wrapper{
    width:46%;
    h2{
      font-size:3.5vw;
      font-weight: 600;
      line-height: 1.3;
      overflow: hidden;
      span{
        display: inline-block;
        span.second{
          margin-left: 15px;
        }
      }
    }
    p{
      font-size:1.03vw;
      color:#333;
      margin:20px 0 30px 0;
    }
    .button-group{
      @extend .dflex;
      width: 70%;
      justify-content: space-between;
      .button{
        @extend .dflex;
        width: 48%;
        height: 56px;
        font-size: 20px;
        font-weight: 600;
        border-radius: 6px;
        &.button-primary{
          background-color: $black;
          color: $white;
          svg{
            margin-left: 8px;
          }
        }
        &.button-secondary{
          background-color: $white;
          color: $black;
          border: 1px solid $black;
          svg{
            margin-left: 8px;
          }
        }
      }
    }
    .review-container{
      margin-top: 50px;
      p.total-reviews{
        font-weight: 600;
        font-size: 20px;
        color:$black;
        margin: 0;
      }
      p.more-reviews{
        color: #333;
        font-weight: 400;
        font-size: 16px;
        color:$black;
        margin: 20px 0 0;
      }
      span{
        @extend .dflex;
        display: inline-flex;
        width: 30px;
        height: 30px;
        border-radius: 5px;
        background-color: $black;
        margin-top: 10px;
        +span{
          margin-left: 9px;
        }
      }
    }

  }
  .right-content-wrapper{
    @extend .dflex;
    transform: scale(1.10);
    width: 54%;
    justify-content: flex-end;
    margin-top: auto;
    margin-bottom: -32px;
    img{
      display: block;
      width: 100%;
      height: auto;
    }
  }
}

.service-container{
  display: block;
  width: 88%;
  margin: 80px auto 0 auto;
  .title-wrapper{
    overflow: hidden;
    .service-title{
      display: block;
      text-align: center;
      color: $dark_green;
      text-transform: uppercase;
      letter-spacing: 2px;
      font-size: 2vw;
      font-weight: 600;
      overflow: hidden;
    }
  }
  h2{
    font-size: 2.25vw;
    font-weight: 600;
    color:$black;
    text-align: center;
    text-transform: capitalize;
    margin-top: 20px;
    line-height: 1.5;
    overflow: hidden;
  }
}

.service-card{
  @extend .dflex;
  flex-wrap: wrap;
  grid-gap:1vw;
  justify-content: flex-start;
  margin: 60px 0;
  .card{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: calc(96%/4);
    border:1px solid rgba(24,24,23,0.17);
    border-radius: 6px;
    background-color: $white;
    padding: 2.90vw 3.3vw;
    height: 18vw;
    position: relative;
    top:0;
    transform: all 0.3s ease-in-out;
    &:hover{
      box-shadow: rgba(0,0,0,0.45) 0px 25px 20px -20px;
      cursor: pointer;
      top: -5px;
    }
    &.dark{
      background-color: $black;
      color:$white;
      .line{
        position: absolute;
        top:0;
        right: 3.5vw;
        width: 4.8vw;;
      }
      h2{
        color:$white;
        text-align: left;
      }
      a{
        display: flex;
        justify-content: space-between;
      }
    }
    .service-icon{
      width: 3.5vw;
      height: 3.5vw;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background-color: $dark_green;
      color: $white;
      border-radius: 10px;
    }
    h3{
      font-size:1.15vw;
      font-weight: 600;
      color: $black;
      text-transform: capitalize;
    }
    a{
      display: inline-flex;
      justify-content: flex-start;
      font-size: 0.95vw;
      color:#181817;
      text-decoration: none;
      align-items: center;
      span{
        margin-right: 8px;
      }
      svg{
        margin-top: 2.5px;
      }
    }
    // .button{
    //   @extend .dflex;
    //   width: 150px;
    //   height: 40px;
    //   font-size: 18px;
    //   border-radius: 6px;
    //   &.learn{
    //     border: 1px solid rgba(24,24,23,0.80);
    //     background-color: #18181716;
    //     color: #181817;
    //   }
    // }
  }
}